import ListContainer from '@/components/Common/ListContainer'
import { getbiuniqueTrade, payTrade, cancleTrade } from '@/api/studyCenter'
import { orderStatus, serviceType } from '@/config/mock'
export default {
  components: {
    ListContainer,
  },
  data () {
    return {
      listFlag: 0,
      loading: false,
      finished: false,
      tableData: [],
      pagination: {
        page: 1
      },
      orderStatus,
      serviceType
    }
  },
  mounted () {
    this.getListHand()
  },
  methods: {
    async getListHand () {
      const params = {
        ...this.pagination
      }
      const { data } = await getbiuniqueTrade(params)
      this.loading = false
      if (data.code === 0) {
        this.tableData = this.pagination.page === 1 ? data.data.entries : this.tableData.concat(data.data.entries)
        if (!this.tableData.length && this.pagination.page === 1) {
          this.listFlag = 2
        } else {
          this.listFlag = 1
          this.pagination.page = this.pagination.page + 1
        }
        if (this.tableData.length >= +(data.data.total)) {
          this.finished = true
        }
      }
    },
    changeFee(fee) {
      return fee.toString().replace(/(?!\b)(?=(\d{3})+\b)/g, ',')
    },
    changeStatus(item, row) {
      if(item.status === 1) {
        if(row.payStatus === 0) {
          return '待支付'
        }
        if(row.payStatus === 2) {
          return '已付款'
        }
      } else if(item.status === 2){
        return '已完成'
      }else if(item.status === 7){
        return '已关闭'
      }
    },
    //支付订单
    async payOrder(item) {
      const { data } = await payTrade(item.tradeNO)
      if(data.code === 0){
        window.location.href = data.data
      }
    },
    //取消订单
    cancleOrder(item) {
      this.$dialog.confirm({
        message: '是否确认取消订单？',
      }).then( async () =>{
        const { data } = await cancleTrade(item.tradeNO)
        if(data.code === 0){
          this.$toast.success('取消成功!')
          this.pagination.page = 1
          this.getListHand()
        }
      }).catch(()=>{})
    }
  },
  head () {
    return {
      title: '服务订单—艾思云课堂'
    }
  }
}
